import { useContext } from "react"
import { ThemeNameContext, themes } from "src/context/ThemeContext"
import { Theme } from "src/themes/light"
import { React, g } from "src/utils/view"
import { motion } from "framer-motion"

export default () => {
  const { themeName, setThemeName, saveTheme, dirty } = useContext(
    ThemeNameContext
  )
  return (
    <ThemeSwitcherContainer>
      <ThemeSwitcher
        whileHover={{ scale: 1.2 }}
        whileTap={{ scale: 0.9 }}
        role="button"
        onClick={() => setThemeName(themeName === "dark" ? "light" : "dark")}
        title="Switch theme"
      >
        {themes[themeName].switcher}
      </ThemeSwitcher>
      <SaveButton
        whileHover={{ scale: 1.2 }}
        whileTap={{ scale: 0.9 }}
        role="button"
        onClick={() => saveTheme()}
        title="Save theme"
        initial={!dirty}
        animate={{ rotate: dirty ? 360 : 0, opacity: dirty ? 1 : 0 }}
        transition={{ duration: 0.4, ease: "easeIn" }}
      >
        <i className="demo-icon icon-floppy"></i>
      </SaveButton>
    </ThemeSwitcherContainer>
  )
}

export function useTheme() {
  const themeName = useThemeName()
  return themes[themeName]
}

export function useThemeName() {
  const { themeName } = useContext(ThemeNameContext)
  return themeName
}

interface Props {
  theme?: Theme
}

const ThemeSwitcher = g(motion.span)`
  cursor: pointer;
`
const ThemeSwitcherContainer = g.span<Props>`
  display: flex;
`
const SaveButton = g(motion.span)<Props>`
  cursor: pointer;
  opacity: 0;
  x: -15px;
`
