import { React } from "src/utils/view"
import seed from "seed-random"

seed("aseed", { entropy: true, global: true })

type Props = {
  children: React.ReactNode
}

export function Random({ children }: Props): React.ReactElement | null {
  const childrenCount = React.Children.count(children)
  if (childrenCount <= 1) return children as React.ReactElement | null
  const arrayOfChildren = React.Children.toArray(children)
  const elementToShow: number = React.useMemo(() => {
    const rnd = Math.random() * arrayOfChildren.length
    return Math.floor(rnd)
  }, [arrayOfChildren.length])
  return arrayOfChildren[elementToShow] as React.ReactElement | null
}
