import { graphql, StaticQuery } from "gatsby"
import Helmet from "react-helmet"
import Emoji from "src/components/Emoji"
import ErrorBoundary from "src/components/ErrorBoundary"
import Header from "src/components/Header"
import { g, React } from "src/utils/view"

import { Random } from "../Random"

import { GlobalStyles } from "./styles"

type Props = {
  children: React.ReactNode
}

declare global {
  interface ExtendedMath extends Math {
    seedrandom(s?: string): string
  }
}

const Layout = ({ children }: Props) => {
  return (
    <ErrorBoundary>
      <StaticQuery
        query={graphql`
          query SiteTitleQuery {
            site {
              siteMetadata {
                title
              }
            }
          }
        `}
        render={data => (
          <>
            <Helmet
              title={data.site.siteMetadata.title}
              meta={[
                { name: "description", content: "nicosommi blog" },
                {
                  name: "keywords",
                  content: [
                    "blog",
                    "engineer",
                    "developer",
                    "javascript",
                    "typescript",
                    "golang",
                    "kubernetes",
                    "docker",
                  ].join(", "),
                },
              ]}
            >
              <html lang="en" />
              <link
                rel="stylesheet"
                type="text/css"
                href="/vendor/fontello/css/fontello.css"
              />
            </Helmet>
            <GlobalStyles />
            <Header siteTitle={data.site.siteMetadata.title} />
            <Content>{children}</Content>
            <Footer>
              <span>Copyright by nicosommi&nbsp;</span>
              <Random>
                <Emoji char="🐱" label="cat" />
                <Emoji char="🤓" label="nerd face" />
                <Emoji char="🦀" label="crab" />
                <Emoji char="🐓" label="rooster" />
                <Emoji char="🥑" label="avocado" />
                <Emoji char="🐒" label="monito" />
              </Random>
            </Footer>
          </>
        )}
      />
    </ErrorBoundary>
  )
}

const Content = g("div")`
  margin: 5% 10%;
  @media (max-width: 500px) {
    margin: 5% 2%;
  }
`

const Footer = g("div")({
  display: "flex",
  justifyContent: "flex-end",
  fontSize: "0.75em",
  lineHeight: "2em",
})

export default Layout
